import React from 'react'
import ReactDOM from 'react-dom'
import './index.css'
import App from './App'
import Fullscreen from './Fullscreen';
import { NuiProvider } from 'fivem-nui-react-lib';

var resource = ""
if (typeof GetParentResourceName != "undefined") { // eslint-disable-line
  resource = GetParentResourceName(); // eslint-disable-line
}

ReactDOM.render(
  <React.StrictMode>
    <NuiProvider resource={resource}>
      <App />
      <Fullscreen/>
    </NuiProvider>
  </React.StrictMode>,
  document.getElementById('root')
)
