export function RouletteClose({ onClick }) {
    return (
        <svg width="38" height="38" viewBox="0 0 38 38" fill="none" xmlns="http://www.w3.org/2000/svg" className="RouletteClose" onClick={onClick}>
            <circle cx="19" cy="19" r="19" fill="#FF2E2E" />
            <path d="M25.5 13L19.25 19.25M13 25.5L19.25 19.25M19.25 19.25L13 13L25.5 25.5" stroke="#252525" strokeOpacity="0.55" strokeWidth="2" />
        </svg>
    )
}

