import { useNuiEvent } from 'fivem-nui-react-lib';
import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import $ from 'jquery';

class Fullscreen extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            play: false,
            opacity: true,
            savedReward: {},
            locales: {
                UI_yourReward: "Your reward",
                UI_close: "Close"
            }
        }
    }
    
    componentDidMount() {
        window.addEventListener("message", (event) => {
            event = event.data;
            if (event.action == "startPlay") {
                this.setState({ play: true })
                this.setState({ opacity: true })
                this.setState({ savedReward: event.data });
            } else if (event.action == "setLocales") {
                this.setState({ locales: event.data })
            }
        })
    }

    close() {
        this.setState({ reward: undefined });
        $.post(`https://${GetParentResourceName()}/exit`) // eslint-disable-line
    }

    render() {
        return (
            <div className="fullscreen">
                {this.state.play &&
                    <video src={require("./case.mp4")} autoPlay className="video" style={{opacity: this.state.opacity ? 1 : 0, transition: "1s"}} onEnded={() => {
                        this.setState({ opacity: false })
                        setTimeout(() => {
                            this.setState({ reward: this.state.savedReward })
                            setTimeout(() => {
                                this.setState({ play: false })
                            }, 500)
                        }, 500)
                    }}/>
                }
                {this.state.reward && 
                    <motion.div 
                        className="reward"
                        animate={{scale: [0, 1.4]}}
                        transition={{type: "just", duration: 0.2}}
                        >
                        <div className="your-reward">{this.state.locales.UI_yourReward}</div>
                        <div className="reward-name">{this.state.reward.name}</div>
                        <div className="icon-wrapper">
                            <img src={process.env.PUBLIC_URL + "/items/" + this.state.reward.icon + ".png"} className="reward-icon"/>
                        </div>
                        <div className="wrapper">
                            <div className="claim-btn" onClick={() => {this.close()}}>{this.state.locales.UI_close}</div>
                        </div>
                        </motion.div>
                    }
            </div>
        )
    }
}

export default Fullscreen;