import React from "react"
import { RouletteBG, RouletteClose } from "./svg"
import RouletteHeader from "./Roulette/RouletteHeader"
import RouletteCases from "./Roulette/RouletteCases"
import RouletteCase from "./Roulette/RouletteCase"
import "./App.scss"
import classNames from "classnames"
import RouletteContainers from "./Roulette/RouletteContainers"
import $ from 'jquery';

class App extends React.Component {
    state = {
        page: 0,
        type: 0,
        caseName: "",
        stats: {
            currentBalance: 0,
            casesOpenedAllTime: 0,
            casesBestDropToday: "Mitsubishi Lancer Evo X"
        },
        cases: [
            // { 
            //     name: "case1", title: "Case #1", price: 320000, icon: "case1", 
            //     items:  [
            //         { name: "item1", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item2", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item3", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item4", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item5", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item6", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item7", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item8", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item9", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item10", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item11", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item12", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item13", title: "Audi 228", type: "Sportca--r", icon: "case1" },
            //         { name: "item14", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item15", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item16", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item17", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item18", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item19", title: "Audi 228", type: "Sportcar", icon: "case1" },
            //         { name: "item20", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //         { name: "item21", title: "Audi 228", type: "Sportcar", icon: "item1" },
            //     ]
            // }
        ],
        userCases: [
            // { name: "case1", title: "Case #1", price: 320000, icon: "case1" }
        ],
        containers: [
            // { name: "container1", title: "Container #1", price: 720000, icon: "container1" },
        ],
        userContainers: [
            // { name: "container1", title: "Container #1", price: 720000, icon: "container1" },
        ],
        show: false,
        canExitOrChange: true,
        locales: {
            UI_containerHeader: "CONTAINER",
            UI_casesHeader: "Cases",
            UI_currentBalance: "Current balance",
            UI_casesOpenedAllTime: "Cases opened for all time",
            UI_bestDrop: "Best drop today",
            UI_my: "My",
            UI_all: "All",
            UI_caseSwitch: "Cases",
            UI_containerSwitch: "Containers",
            UI_buy: "Buy",
            UI_open: "Open",
            UI_whatsInside: "What's inside?"
        }
    }

    onChangeType = (i) => {
        if (!this.state.canExitOrChange) return
        if (this.state.page === 1) this.setState({ page: 0, type: i })
        else this.setState({ type: i })
    }

    onBuy = (name) => {
        $.post(`https://${GetParentResourceName()}/buyCase`, JSON.stringify({name: name})) // eslint-disable-line
    }
    onEnter = (name) => {
        this.setState({ page: 1, caseName: name })
    }

    onClose = () => {
        $.post(`https://${GetParentResourceName()}/exit`) // eslint-disable-line
    }

    componentDidMount() {
        window.addEventListener("message", (event) => {
            event = event.data;
            if (event.action == "setShow") {
                this.setState({ show: event.data });
            } else if (event.action == "setCases") {
                this.setState({ cases: event.data });
            } else if (event.action == "setUserCases") {
                this.setState({ userCases: event.data });
            } else if (event.action == "setContainers") {
                this.setState({ containers: event.data });
            } else if (event.action == "setUserContainers") {
                this.setState({ userContainers: event.data });
            } else if (event.action == "setStats") {
                this.setState({ stats: event.data })
            } else if (event.action == "setPage") {
                this.setState({ page: event.data })
            } else if (event.action == "setType") {
                this.setState({ type: event.data })
            } else if (event.action == "setCaseName") {
                this.setState({ caseName: event.data })
            } else if (event.action == "setCanExit") {
                this.setState({ canExitOrChange: event.data });
            } else if (event.action == "setLocales") {
                this.setState({ locales: event.data })
            }
        })
    }

    render() {
        const page = this.state.page
        const type = this.state.type
        const caseName = this.state.caseName
        const stats = this.state.stats
        const cases = this.state.cases
        const userCases = this.state.userCases
        const containers = this.state.containers
        const userContainers = this.state.userContainers
        const locales = this.state.locales

        let bg = {
            width: 1465,
            height: 904
        }
        if (page === 2)
            bg.height = 667

        let infoIndex = cases.findIndex((i) => i.name === caseName)
        let info = cases[infoIndex]

        return (
            <>
                {this.state.show && 
                    <div className="App">
                        <div className={classNames({ "wrap": true, "containers": page === 2 })}>
                            <RouletteBG width={bg.width} height={bg.height} page={page} />
                            <div className="title">{page == 2 ? "Containers" : "CASES"}</div>
                            <RouletteHeader
                                type={type}
                                stats={stats}
                                onChangeType={this.onChangeType}
                                page={page}
                                canExitOrChange={this.state.canExitOrChange}
                                locales={locales}
                            />

                            {page === 0 &&
                                <RouletteCases
                                    type={type}
                                    cases={cases}
                                    userCases={userCases}
                                    onBuy={this.onBuy}
                                    onEnter={this.onEnter}
                                    locales={locales}
                                />
                            }
                            {page === 1 && info &&
                                <RouletteCase
                                    info={info}
                                    locales={locales}
                                />
                            }
                            {page === 2 && 
                                <RouletteContainers
                                    containers={containers}
                                    userContainers={userContainers}
                                    type={type}
                                    locales={locales}
                                />
                            }

                            {this.state.canExitOrChange &&
                                <RouletteClose onClick={() => this.onClose()} />
                            }
                        </div>
                    </div>
                }
            </>
        )
    }
}

export default App