import React from "react"
import classNames from "classnames"
import svg from "../svg"
import { spacingNumber } from "../helpers"

const RouletteHeader = ({ type, stats, onChangeType, page, canExitOrChange, locales }) => (
    <div className="RouletteHeader">
        <div className="switch">
            <div className={classNames({ "item": true, "active": type === 0 })} onClick={() => onChangeType(0)}>{locales.UI_all} {page == 2 ? locales.UI_containerSwitch : locales.UI_caseSwitch}</div>
            <div className={classNames({ "item": true, "active": type === 1 })} onClick={() => onChangeType(1)}>{locales.UI_my} {page == 2 ? locales.UI_containerSwitch : locales.UI_caseSwitch}</div>
        </div>
        <div className="item current-balance">
            {svg.headerCurrentBalance}
            <div className="wrap">
                <span>{locales.UI_currentBalance}</span>
                <span>{locales.UI_currency}{spacingNumber(stats.currentBalance)}</span>
            </div>
        </div>
        <div className="item cases-opened-all-time">
            {svg.headerCasesOpenedAllTime}
            <div className="wrap">
                <span>{locales.UI_casesOpenedAllTime}</span>
                <span>{stats.casesOpenedAllTime}</span>
            </div>
        </div>
        <div className="item cases-best-drop-today">
            {svg.headerCasesBestDropToday}
            <div className="wrap">
                <span>{locales.UI_bestDrop}</span>
                <span>{stats.casesBestDropToday}</span>
            </div>    
        </div>
    </div>
)

export default RouletteHeader