import {spacingNumber} from "../helpers"
import "./Components.scss"

const CasesCase = ({ info, isUser, onBuy, onEnter, locales }) => (
    <div className="CasesCase">
        <img src={`cases/${info.icon}.png`} />
        <div className="title">{info.title}</div>
        <div className="buy">
            {!isUser && <div className="button" onClick={onBuy}>{locales.UI_buy}</div>}
            {isUser && <div className="button" onClick={onEnter}>{locales.UI_open}</div>}
            <div className="price">{locales.UI_currency}{spacingNumber(info.price)}</div>
        </div>
    </div>
)

const RouletteCases = ({ type, cases, userCases, onBuy, onEnter, locales }) => {
    const casesList = (type === 0 ? cases : userCases).map((info, i) => 
        <CasesCase
            key={i}
            info={info}
            isUser={type === 1}
            onBuy={() => onBuy(info.name)}
            onEnter={() => onEnter(info.name)}
            locales={locales}
        />
    )

    return (
        <div className="RouletteCases">
            {casesList}
        </div>
    )
}
export default RouletteCases