import classNames from "classnames"
import React from "react"
import { spacingNumber } from "../helpers"
import svg from "../svg"
import "./Components.scss"
import clickSound from './click.wav';
import finishSound from './finished.mp3'
import $ from 'jquery';

const RouletteCaseItem = ({ item }) => (
    <div className="RouletteCaseItem">
        <img src={`items/${item.icon}.png`} />
        <div className="title">{item.title}</div>
        <div className="type">{item.type}</div>
    </div>
)

class RouletteCase extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            type: 0, // idle - 0 | 1 - prepare | run - 1 | finish - 2
            centerItem: {
                icon: "",
                title: "",
                type: ""
            },
            rouletteItems: [],
            rouletteInterval: null
        }
    }

    componentDidMount() {
        this.randomFillRouletteItems()
    }

    randomFillRouletteItems() {
        const items = this.props.info.items
        const rouletteItems = []
        for (let i = 0; i < 100; i++) {
            let rand = Math.floor(Math.random() * items.length)
            let item = {...items[rand]}
            if (item) {
                item.title = `${item.title}`
                rouletteItems.push(item)
            }
        }

        this.setState({ rouletteItems })
        this.interval()
    }

    start() {
        $.post(`https://${GetParentResourceName()}/openCase`, JSON.stringify({name: this.props.info.name})).done((data) => { // eslint-disable-line
            if (data) {
                let type = this.state.type
                if (type > 0) {
                    this.setState(
                        { type: 0 },
                        () => setTimeout(() => this.start(), 50)
                    )
                    return
                }
        
                let interval = setInterval(() => this.interval(), 10)
                this.setState(
                    { type: 1, rouletteInterval: interval },
                    () => setTimeout(() => this.setState({ type: 2 }), 100)
                )
                setTimeout(() => this.stop(), 13 * 1000)
            }
        })
        
    }
    interval() {
        let items = document.querySelectorAll(`.Roulette .RouletteCaseItem`)
        for (let i = items.length - 1; i > 0; i--) {
            let item = items[i]            
            let rect = item.getClientRects()[0]
            if (rect.left < (900 * (window.innerWidth / 1920))) {
                let rouletteItem = this.state.rouletteItems[i]
                if (rouletteItem != this.state.centerItem) {
                    var audio = new Audio(clickSound);
                    audio.volume = 0.2;
                    audio.play();
                }
                this.setState({ centerItem: rouletteItem })
                break;
            }
        }
    }
    // interval() {
    //     let items = document.querySelectorAll(`.Roulette .RouletteCaseItem`)
    //     for (let i = items.length - 1; i > 0; i--) {
    //         let item = items[i]
    //         let rect = item.getClientRects()[0]
    //         if (rect.left < 1116) {
    //             let rouletteItem = this.state.rouletteItems[i]
    //             if (rouletteItem != this.state.centerItem) {
    //                 var audio = new Audio(clickSound);
    //                 audio.volume = 0.2;
    //                 audio.play();
    //             }
    //             this.setState({ centerItem: rouletteItem })
    //             break;
    //         }
    //     }
    // }
    stop() {
        setTimeout(() => {
            var audio = new Audio(finishSound);
            audio.volume = 0.2;
            audio.play();
            clearInterval(this.state.rouletteInterval)
            $.post(`https://${GetParentResourceName()}/caseReward`, JSON.stringify({case: this.state.centerItem, curcase: this.props.info.name})) // eslint-disable-line
            this.setState({ type: 3, rouletteInterval: null })
        }, 500)
    }

    render() {
        const type = this.state.type
        const centerItem = this.state.centerItem
        const info = this.props.info
        const rouletteItemsList = this.state.rouletteItems.map((item, i) =>
            <RouletteCaseItem
                key={i}
                item={item}
            />
        )
        const itemsList = (info.items || []).map((item, i) =>
            <RouletteCaseItem
                key={i}
                item={item}
            />
        )
        return (
            <div className="RouletteCase">
                <div className="Roulette">
                    <div className="RouletteItems">
                        <div className={classNames({ "items": true, [`type-${type}`]: true })}>
                            {rouletteItemsList}
                        </div>
                    </div>
                    <div className="center">
                        {centerItem.icon && <img src={`items/${centerItem.icon}.png`} />}
                        <div className="title">{centerItem.title}</div>
                        <div className="type">{centerItem.type}</div>
                        {svg.rouletteCenterCursor}
                    </div>
                    {(type === 0 || type === 3) &&
                        <div className="actions">
                            <div className="case-title">{info.title}</div>
                            <div className="open" onClick={() => this.start()}>
                                <div className="button">{this.props.locales.UI_open}</div>
                                <div className="price">{this.props.locales.UI_currency}{spacingNumber(info.price)}</div>
                            </div>
                        </div>
                    }
                </div>
                <div className="RouletteItems">
                    <div className="title">{this.props.locales.UI_whatsInside}</div>
                    <div className="items">
                        {itemsList}
                    </div>
                </div>
            </div>
        )
    }
}

export default RouletteCase