import { spacingNumber } from "../helpers"
import "./Components.scss"
import $ from 'jquery';

const onBuy = (name) => {
    $.post(`https://${GetParentResourceName()}/buyContainer`, JSON.stringify({name: name})) // eslint-disable-line
}

const onOpen = (name) => {
    $.post(`https://${GetParentResourceName()}/openContainer`, JSON.stringify({name: name})) // eslint-disable-line
}

const RouletteContainer = ({ container, type, locales }) => (
    <div className="RouletteContainer">
        <img src={`containers/${container.icon}.png`} />
        <div className="title">{container.title}</div>
        <div className="buy">
            {type === 0 && <div className="button" onClick={() => {onBuy(container.name)}}>{locales.UI_buy}</div>}
            {type === 1 && <div className="button" onClick={() => {onOpen(container.name)}}>{locales.UI_open}</div>}
            <div className="price">{locales.UI_currency}{spacingNumber(container.price)}</div>
        </div>
    </div>
)

const RouletteContainers = ({ containers, userContainers, type, locales }) => {
    const containersList = (type === 0 ? containers : userContainers).map((container, i) =>
        <RouletteContainer
            key={i}
            container={container}
            type={type}
            locales={locales}
        />
    )
    return (
        <div className="RouletteContainers">
            {containersList}
        </div>
    )
}

export default RouletteContainers